@import '../../assets/scss/colors';
@import '../../assets/scss/utils';

#logo {
    svg {
        height: 70px;
    }
}
.site {

    &__navigation {
        &, a {
            color: $white;
            text-decoration: none;
        }
        a:hover {
            text-decoration: underline;
        }
        width: 100%;
        top: 0;
        z-index: 99;

        .logo--blue {
            display: none;
        }
        .main-navigation {
            display: none;
            margin: 0 auto;
            font-size: 24px;
            line-height: 28px;
            text-transform: none;
            li {
                margin-left: 1em;
                line-height: 1em;
                &:hover, &.active {
                    a { 
                        text-decoration: underline;
                        text-decoration-color: $red;
                    }
                }
            }
        }
        &--white,
        &.navigation--sticky {
            &, a {
                color: $black;
            }
            .main-navigation {
                display: block;
            }
            
            .site__logo svg .cls-2 {
                fill: $black;
            }
        }

        &--white {
            background-color: $white;
            padding: 28px 0;
            position: sticky;
            top: 0;
            z-index: 999;
            .menu--mobile-float {
                background-color: $white;
                display: flex;
                align-items: flex-end;
                .menu {
                    margin: 0
                }
            }
            .sign-in.dropdown-menu {
                .handle::after {
                    border-color: $black transparent transparent transparent!important;
                }
            }
        }
        .hamburger {
            cursor: pointer;
            display: none;
            margin-left: auto;
            z-index: 1;
            width: 24px;
            height: 24px;
            background: $black;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            /* Hamburger Line */
            & > div {
                position: relative;
                flex: none;
                width: 100%;
                background: $white;
                display: flex;
                align-items: center;
                justify-content: center;
                transform-origin: center;
                top: 50%;
                height: 2px;
                transform: translate(0, -50%);
    
                /* Hamburger Lines - Top & Bottom */
                &::after,
                &::before {
                    content: '';
                    position: absolute;
                    z-index: 1;
                    top: 50%;
                    width: 100%;
                    height: 2px;
                    background: inherit;
                    transition: transform 0.4s ease;
                }
    
                &::after {
                    transform: rotate(0deg) translate(0, 150%);
                }
                &::before {
                    transform: rotate(0deg) translate(0, -250%);
                }
            }
        }

        

        // Menu Float on mobile
        .menu--mobile-float {
            @extend .flex;
            flex-grow: 1;
            flex-shrink: 1;
            justify-content: flex-end;
        }
        
        .sign-in.dropdown-menu {
            pointer-events: all;
            margin-right: 1em;
            line-height: 2em;
            padding: 0 1em;
            .handle {
                padding-right: 1em;
                cursor: pointer;
                text-transform: uppercase;
                // &:hover {
                //     text-decoration: underline;
                //     color: $red;
                //     text-decoration-color: $red;
                // }
                &::after {
                    content: " ";
                    width: 0;
                    height: 0;
                    border-width: 3px 3px 0 3px;
                    border-color: $white transparent transparent transparent;
                    border-style: solid;
                    position: absolute;
                    right: 0;
                    top: 50%;
                    margin-top: -2px;
                }
            }
            .menu {
                border-radius: 2px;
                right: 1em;
                top: 100%;
                padding: 20px 0;
                border: none;
                li {
                    line-height: 2em;
                    background-color: $white;
                    &:first-child {
                        border-top-left-radius: 2px;
                        border-top-right-radius: 2px;
                    }
                    &:last-child {
                        border-bottom-left-radius: 2px;
                        border-bottom-right-radius: 2px;
                    }
                }
                &::after{
                    content:''; /* Required to display content */
                    position: absolute; /* Sets the position absolute to the top div */
                    top: 18px;
                    right: 4px;/* position the little arrow */
                    margin-left: -4px;
                    margin-top:-4px; /* Set margin equal to border px */
                    width: 0;
                    z-index:1;
                    height: 0;
                    border-bottom: solid 6px #FFF; /* Creates the arrow pointing up, to change to a notch instead user border-top */
                    border-left: solid 4px transparent; /* Creates triangle effect */
                    border-right: solid 4px transparent; /* Creates triangle effect */
                    border-radius: none;
                }
                
                &, a {
                    color: $black;
                }
                li {
                    padding: 0 1em;
                }
                a {
                    white-space: nowrap;
                    line-height: 1.75em;
                }
            }
        } 
        .languages.dropdown-menu {
            font-weight: 300;
            .handle {
                color: $red;
                text-decoration: underline;
                text-decoration-color: $red;
                display: inline-block;
                width: 2em;
                line-height: 2em;
                text-align: center;
            }
            .menu {
                right: 0;
                width: 100%;
                text-align: center;
                li {
                    line-height: 1.75em;
                    display: block;
                    &.active {
                        display: none;
                    }
                    a {
                        line-height: 2em;
                    }
                }
            }
            // Decoration for corners
            .handle::after,
            .handle::before,
            .menu::after,
            .menu::before,
            &::after,
            &::before {
                content: ' ';
                width: 4px;
                height: 4px;
                position: absolute;
                top: 0;
                left: 0;
                border-width: 1px 0 0 1px;
                border-color: $white;
                border-style: solid;
                display: block;
            }
            .handle::after {
                left: auto;
                right: 0;
                border-width: 1px 1px 0 0;
            }
            .menu::after,
            .menu::before {
                display: none;
            }

            .menu::after,
            &::after {
                top: auto;
                bottom: 0;
                border-width: 0px 0px 1px 1px;
            }
            .menu::before,
            &::before {
                top: auto;
                bottom: 0;
                right: 0;
                left: auto;
                border-width: 0px 1px 1px 0px;
            }
            &--open {
                &::after,
                &::before {
                    display: none;
                }
                
                .menu::after,
                .menu::before {
                    display: block;
                }
            }
        }
    }
    
    &--shown-navigation {
        .hamburger {
            > div {
                background-color: transparent;
                &::after,
                &::before {
                    transform-origin: center;
                    background-color: $white;

                }
                &::after {
                    transform: translate(0, -50%) rotate(45deg);
                }
                &::before {
                    transform: translate(0, -50%) rotate(-45deg);
                }
            }
        }
    }
}

@mixin mobile() {
    .hide-mobile {
        display: none!important;
    }

    .d-mobile {
        display: block !important;
    }
    .container {
        width: 90%;
        .wrap--80 {
            width: 100%;
        }
    }
    
    .section {
        padding-top: 50px;
        padding-bottom: 0px;
    }
    .site {
        &__logo a {
            height: 44px;
        }
        &__navigation {
            .container {
                height: 70px;
                align-items: flex-start;
            }
            &--white,
            &.navigation--sticky {
                padding: 28px 0px;
                justify-content: flex-start;
                .hamburger {
                    margin-bottom: 5px;
                    border-radius: 50%;
                    padding: 5px;
                    align-items: center;
                    flex-direction: column;
                    justify-content: center;
                    background-color: $red;
                }
            }
            .menu--mobile-float {
                position: absolute;
                z-index: 99;
                display: none;
                width: 100vw;
                left: 0;
                box-sizing: border-box;
                background-color: $black;
                min-height: 70vh;
                margin-top: 36px;
                margin-left: -5vw;
                padding: 5vw;

                top: 100%;
                left: 0;
                .navigation {
                    text-transform: none;
                    .menu {
                        flex-direction: column;
                        text-align: left;
                        li {
                            margin-left: 0;
                            font-size: 24px;
                            line-height: 34px;
                            &:hover,
                            &.active {
                                &, a {
                                    color: $red!important;
                                    text-decoration: none!important;
                                }
                            }
                        }
                        &:first-child {
                            margin-bottom: 50px;
                        }
                    }
                }
            }
            .hamburger {
                display: block;
                margin-left: auto;
            }
            .navigation.margin--l {
                display: flex;
                flex-direction: row;
                flex-wrap: nowrap;
            }
            .dropdown-menu {
                .handle {
                    display: none!important;
                    &::after {
                        display: none;
                    }
                }
                .menu {
                    display: block;
                    position: relative;
                    left: 0;
                    &::after {
                        display: none;
                    }
                }
                &.sign-in {
                    flex-shrink: 1;
                    flex-grow: 1;
                    padding-left: 0;
                    .menu {
                        background: none;
                        &, a {
                            color: inherit;
                            text-decoration-color: $red;
                        }
                        li {
                            padding-left: 0;
                            padding-right: 0;
                            font-size: 18px;
                            line-height: 28px;
                            text-decoration: underline;
                            text-decoration-color: $red;
                            background-color: transparent;
                        }
                    }
                }
                &.languages {
                    flex-grow: 0;
                    flex-shrink: 0;
                    &::after,
                    &::before {
                        display: none;
                    }
                    .menu {
                        &::after,
                        &::before {
                            display: none;
                        }
                        display: flex;
                        flex-direction: row;
                        justify-content: space-around;
                        li {
                            display: block!important;
                            position: relative;
                            width: 2em;
                            line-height: 2em;
                            text-align: center;
                            margin-right: 12px;
                            &.active a {
                                color: $red!important;
                            }
                        }
                        li::after,
                        li a::after,
                        li::before,
                        li a::before {
                            content: ' ';
                            width: 4px;
                            height: 4px;
                            position: absolute;
                            top: 0;
                            left: 0;
                            border-width: 1px 0 0 1px;
                            border-color: $white;
                            border-style: solid;
                            display: block;
                        }
                        li a::after {
                            left: auto;
                            right: 0;
                            border-width: 1px 1px 0 0;
                        }

                        li::before {
                            top: auto;
                            bottom: 0;
                            border-width: 0 0 1px 1px;
                        }

                        li a::before {
                            top: auto;
                            bottom: 0;
                            left: auto;
                            right: 0;
                            border-width: 0 1px 1px 0px;
                        }
                        li.active {
                            &::before,
                            &::after,
                            a::before,
                            a::after {
                                border-color: $red;
                            }
                        }
                    }
                }
            }
            &--white {
                background-color: transparent;
                .brand {
                    display: none;
                }
                .container {
                    background-color: inherit;
                }
                .menu--mobile-float {
                    margin-top: 10px;
                    padding-top: 70px;
                    background-color: inherit;
                }
                .dropdown-menu.languages {
                    .menu li {
                        &::before,
                        &::after,
                        a::before,
                        a::after {
                            border-color: $black;
                        }
                    }
                }
            }
        }
        &--shown-navigation {
            .site__navigation {
                background-color: $black;
                &--white {
                    .site__logo svg .cls-2 {
                        fill: $white;
                    }
                    &, .container {
                        background-color: $black;
                        color: $white;
                    }
                    .brand {
                        display: block;
                    }
                }
                .menu--mobile-float {
                    display: block!important;
                    background-color: $black;
                    color: $white;
                    .navigation .menu li a {
                        color: $white;
                    }
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {
    @include mobile();
}

@media only screen and (min-width: 768px) and (max-width: 1280px) and (orientation: portrait) {
    /* CSS */
    @include mobile();
}

@media screen and ( min-width: 769px ) {
    .site {
        &__navigation {
            &--white {
                .menu--mobile-float {
                    align-items: flex-end;
                }
                .navigation {
                    display: flex;
                    flex-direction: column;
                    .sign-in {
                        font-size: 0.9em;
                        margin-right: 0;
                        .handle {
                            padding-right: 0;
                            padding-left: 1em;
                            &::after {
                                right: auto;
                                left: 0;
                            }
                        }
                    }
                    .languages.dropdown-menu {
                        .handle {
                            display: none;
                        }
                        &::before,
                        &::after {
                            display: none;
                        }
                        ul {
                            position: relative;
                            display: flex;
                            flex-direction: row;
                            li {
                                display: block!important;
                                a {
                                    font-weight: 400;
                                    padding: 0 0.25em;
                                    line-height: 1em;
                                    &::after {
                                        content: " ";
                                        display: inline-block;
                                        height: 0.75em;
                                        width: 2px;
                                        background-color: $black;
                                        margin-left: 0.5em;
                                    }
                                }
                                &:last-child {
                                    a {
                                        padding-right: 0;
                                        &::after {
                                            display: none;
                                        }
                                    }
                                }
                                &.active {
                                    &, a {
                                        color: $red;
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .languages.dropdown-menu:hover {
                
            }
        }
    }
}

@media screen and (min-width: 768px) and (max-width: 1366px) and (orientation: landscape) {
    .container {
        width: calc(100vw - 40px);
    }
}

@media screen and ( min-width: 769px) and (max-width: 900px) {
    .site {
        &__navigation--white {
            .brand {
                margin: 8px auto;
            }
        }
    }
}
@media screen and (min-width: 901px) and (max-width: 1366px) {
    .container {
        width: calc(100vw - 80px);
    }
}

@media screen and (min-width: 901px) {
    .site {
        &__navigation--white {
            .navigation {
                .sign-in {
                    order: 2;
                }
                .menu--langauges {
                    order: 1;
                }
            }
        }
    }
}
@media screen and (min-width: 901px) and (max-width:1280px)  { 
    /* tablet, landscape iPad, lo-res laptops ands desktops */ 

    .site {
        &__logo a {
            height: 60px;
        }
        &__navigation .main-navigation {
            font-size: 20px!important;
            line-height: 30px;
        }
    }
}